.secao-banner{
    display: flex;
    align-items: center;
    width: 100%;
    height: 95vh;
}

.banner-tema-dark{
    height: 100%;
    width: 50%;
    background-color: #252525;
    display: flex;
    justify-content: center;
    align-items: center;
}

.banner-tema-light{
    height: 100%;
    width: 50%;
    background-color: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
}

.img-banner{
    width: 100%;
}

.banner-conteudo{
    background-color: #fafbfc;
    width: 50%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

.secao-banner p{
    text-align: left;
    font-size: 12px;
    letter-spacing: 3px;
    color: #022144;
    margin: 0;
}

.secao-banner h1{
    text-align: left;
    font-size: 40px;
    font-weight: 400;
    color: #022144;
    margin: 0;
}

.secao-banner span{
    font-size: 40px;
    font-weight: bold;
    color: #022144;
    margin: 0;
}


@media screen and (max-width: 768px){
    .banner-conteudo{
        background-color: #fafbfc;
        width: 100%;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
    }
    
    .img-banner{
        width: 0;
    }
    .banner-tema-dark{
        height: 100%;
        width: 0;
        background-color: #252525;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    
    .banner-tema-light{
        height: 100%;
        width: 0;
        background-color: #fff;
        display: flex;
        justify-content: center;
        align-items: center;
    }
}